.watch {
  background-color: #19e1bf;
  border: 4px dashed #8b0000;
  border-radius: 50%;
  width: 80vh;
  height: 80vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.watch__center {
  z-index: 3;
  background-color: #d034db;
  border-radius: 50%;
  width: 3.2vh;
  height: 3.2vh;
  position: absolute;
  top: 50%;
  left: 51%;
  transform: translate(-50%, -50%);
}

.watch__second-line {
  transform-origin: 100% 100%;
  z-index: 2;
  background-color: #fff;
  border-top: 10px solid red;
  border-radius: 50%;
  width: 6px;
  height: 40vh;
  animation: 5s linear infinite watch;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.watch__minute-line {
  transform-origin: 100% 100%;
  z-index: 2;
  background-color: gray;
  border-top: 10px solid red;
  border-radius: 50%;
  width: 12px;
  height: 40vh;
  animation: 300s linear infinite watch;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.watch__hour-line {
  transform-origin: 100% 100%;
  z-index: 1;
  background-color: #dea418;
  border-top: 10px solid red;
  border-radius: 50%;
  width: 24px;
  height: 20vh;
  animation: 3600s linear infinite watch;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

@keyframes watch {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

